* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  color: #237be0;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 3px 10px;
  font-size: 1.2rem;
  box-shadow: 1px 1px 5px #0000004d;
}

button:hover {
  color: #fff;
  background-color: #237be0;
}

button[disabled] {
  color: #949292;
  background-color: #eeebeb;
}

.form button {
  margin-top: 10px;
}

.timer {
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.field {
  text-align: center;
  color: #237be0;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0 5px;
  font-size: 1.2rem;
  font-weight: 400;
  box-shadow: 1px 1px 5px #0000004d;
}

.value {
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
}

#datetime-picker {
  color: #237be0;
  text-align: center;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 3px;
  font-size: 1.2rem;
  box-shadow: 1px 1px 5px #0000004d;
}

#datetime-picker:hover {
  color: #fff;
  background-color: #237be0;
}

